.smart-multicarousel button.arrow-prev,
.smart-multicarousel button.arrow-next {
  position: absolute;
  top: 50%;
  display: block;
  z-index: 1;
  width: 68px;
  height: 68px;
  margin-top: -69.5px;
  background: none;
  border: none;
  opacity: 0.5;
  -webkit-appearance: button;
  cursor: pointer;
}
.smart-multicarousel button.arrow-next {
  right: 0;
}
.smart-multicarousel button.arrow-prev {
  left: 0;
}
.smart-multicarousel .icon-arrow-right,
.smart-multicarousel .icon-arrow-left {
  display: block;
  height: 100%;
  background: url(data:image/svg+xml;base64,) no-repeat;
}
.smart-multicarousel .icon-arrow-right {
  transform: scaleX(-1);
}
.smart-multicarousel .smart-multicarousel-dots {
  position: absolute;
  width: 100%;
  height: auto;
  bottom: 30px;
  line-height: 0;
  text-align: center;
  opacity: 1;
  transition: opacity 0.5s ease-out;
  cursor: default;
}
.smart-multicarousel .carousel-dot {
  display: inline-block;
  width: 9px;
  height: 9px;
  margin: 0 5px;
  vertical-align: middle;
  background: rgba(118, 118, 118, 0.5);
  border: none;
  border-radius: 24px;
  cursor: pointer;
}
.smart-multicarousel .active-dot {
  width: 11px;
  height: 11px;
  background: rgba(255, 255, 255, 0.5);
  border: 1px solid #bababa;
}
.smart-multicarousel {
  display: flex;
  position: relative;
  overflow: hidden;
  justify-content: center;
}
.smart-multicarousel [data-slide-area] {
  display: flex;
  position: relative;
  overflow: hidden;
  padding-bottom: 71px;
  width: 780px;
}
.smart-multicarousel [data-slide-item] {
  position: relative;
  min-width: 250px;
  z-index: 1;
  margin: 0 5px;
  min-height: 150px;
  background-color: aliceblue;
  transition: transform 2s ease-in-out;
}
.smart-multicarousel .left {
  transform: translateX(-780px);
}
.smart-multicarousel .right {
  position: absolute;
  transform: translateX(-780px);
}
.smart-multicarousel .active-slide {
  display: block;
  position: relative;
  visibility: visible;
  z-index: 2;
  opacity: 1;
}
.smart-multicarousel .prev-slide {
  transform: translateX(-780px);
  z-index: 1;
  display: block;
}
.smart-multicarousel .left-sibling {
  transform: translateX(-1560px);
}
.smart-multicarousel .prev {
  visibility: visible;
  position: relative;
}
smart-image {
  display: block;
  width: 100%;
  height: 0;
  padding: 0;
  text-align: center;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}
smart-image[mode='origin'] {
  display: inline-block;
  width: auto;
  height: auto;
}
smart-image[mode='cover'] {
  height: 100%;
}
smart-image[mode='save-ratio'] {
  height: 0;
}
smart-image[mode='save-ratio']:not([loaded]) {
  min-height: 1px;
}
smart-image:after {
  display: none;
  content: attr(alt);
}
smart-image[error]:after {
  display: inline-block;
}
smart-image[loaded]:after {
  display: none;
}
smart-image.bg-flex {
  background-size: 100% 100%;
}
smart-image.bg-inscribe {
  background-size: contain;
}
smart-image.bg-v-top {
  background-position-y: top;
}
smart-image.bg-v-center {
  background-position-y: center;
}
smart-image.bg-v-bottom {
  background-position-y: bottom;
}
smart-image.bg-h-left {
  background-position-x: left;
}
smart-image.bg-h-center {
  background-position-x: center;
}
smart-image.bg-h-right {
  background-position-x: right;
}
.img-container {
  position: relative;
  overflow: hidden;
}
.img-container smart-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  display: block;
}
.img-container smart-image[mode="origin"] {
  position: relative;
  width: auto;
  height: auto;
}
.img-container.img-noratio smart-image {
  position: relative;
}
.img-container smart-image[lazy] {
  opacity: 0;
}
.img-container smart-image[lazy][ready] {
  opacity: 1;
  -webkit-transition: opacity 0.4s;
  transition: opacity 0.4s;
}
.img-container.img-container-no-rat smart-image {
  position: relative;
}
.img-container.img-container-16-9 {
  padding-top: 56.25%;
}
